@import './../../../styles/variables.scss';

.planSection {
  position: relative;
  width: 100%;
  height: 907px;
  background: url('./../../../assets/img/planSection/sky.svg');
  display: flex;
  justify-content: center;
  overflow: hidden;

  &__papper__plane {
    position: absolute;
    left: -150px;
    top: 120px;
    z-index: 2;
  }

  &__plane-path {
    position: absolute;
    left: calc(50% - 1050px);
    top: 140px;
    visibility: hidden;
  }

  &__wrapper {
    flex: 0 1 1395px;
  }

  &__title {
    margin-top: 42px;
    text-align: center;
  }

  &__title-accent {
    position: relative;

    &::after {
      position: absolute;
      content: url('./../../../assets/img/planSection/wavyUnderline.svg');
      top: 17px;
      left: 0;
    }
  }

  &__plan {
    position: relative;
    margin-top: 87px;
    height: 500px;
  }

  &__dotted-line {
    position: absolute;
    left: 91px;
    top: 45px;
  }


  &__circle {
    height: 80px;
    width: 80px;
    border-radius: 100%;
    background-color: $additionalAccentColor;
    color: $mainBackgroundColor;
    font-weight: 700;
    font-size: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__stepOne-circle {
    position: absolute;
    top: 0;
    left: 23px;
  }

  &__stepOne-text {
    position: absolute;
    top: 0;
    left: 163px;
    max-width: 230px;
    line-height: 24px;
    letter-spacing: 0.04em;
    opacity: 0;
  }

  &__stepTwo-circle {
    position: absolute;
    left: 491px;
    top: 203px;
  }

  &__stepTwo-text {
    position: absolute;
    top: 203px;
    left: 630px;
    max-width: 246px;
    line-height: 24px;
    letter-spacing: 0.04em;
    opacity: 0;
  }

  &__stepThree-circle {
    position: absolute;
    left: 971px;
    top: 419px;
  }

  &__stepThree-text {
    position: absolute;
    top: 419px;
    left: 1112px;
    max-width: 290px;
    line-height: 24px;
    letter-spacing: 0.04em;
    opacity: 0;
  }
}